}

.customer-info {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.customer-name {
  font-weight: 600;
  color: #333;
  font-size: 1rem;
}

.customer-notes-preview {
  background: #e3f2fd;
  border: 1px solid #bbdefb;
  border-radius: 6px;
  padding: 0.4rem 0.6rem;
  margin-top: 0.3rem;
}

.notes-preview-text {
  color: #1976d2;
  font-size: 0.85rem;
  font-style: italic;
  line-height: 1.3;
}

.customer-actions {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-shrink: 0;
}

.edit-notes-btn:hover {
  background-color: #1976d2 !important;
  transform: translateY(-1px);
}

.add-advance-btn:hover {
  background-color: #f57c00 !important;
  transform: translateY(-1px);
}

.customer-entry .delete-btn {
  background: #f44336;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.3rem 0.6rem;
  font-size: 0.8rem;
  cursor: pointer;
  transition: background 0.2s;
}

.customer-entry .delete-btn:hover {
  background: #d32f2f;
}

/* Responsive customer list */
@media (max-width: 768px) {
  .customer-entry {
    flex-direction: column;
    align-items: stretch;
    gap: 0.8rem;
  }
  
  .customer-actions {
    justify-content: flex-start;
    flex-wrap: wrap;
  }
  
  .customer-notes-preview {
    margin-top: 0.5rem;
  }
}

.unpaid-badge {
  background: #ffebee;
  color: #c62828;
  padding: 0.2rem 0.5rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: 500;
  margin-right: 0.5rem;
  border: 1px solid #ffcdd2;
}

.purchases-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.month-section {
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  overflow: hidden;
}

.month-section h3 {
  background: #f5f5f5;
  margin: 0;
  padding: 0.8rem 1rem;
  font-size: 1.1rem;
  color: #333;
  border-bottom: 1px solid #e0e0e0;
}

.month-items {
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.dashboard-section {
  background: #fff;
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.dashboard-section h2 {
  color: #1976d2;
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
  text-align: center;
}

.dashboard-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 8px;
  margin-bottom: 0.8rem;
  border: 1px solid #e9ecef;
}

.dashboard-item:last-child {
  margin-bottom: 0;
}

.app-nav {
  display: flex;
  background: #fff;
  border-radius: 12px;
  padding: 0.5rem;
  margin: 0.75rem 1rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  overflow-x: auto;
  gap: 0.3rem;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.nav-btn {
  background: #f8f9fa;
  color: #666;
  border: none;
  border-radius: 8px;
  padding: 0.8rem 1.2rem;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.2s;
  white-space: nowrap;
  flex: 1;
  min-width: 80px;
}

.nav-btn:hover {
  background: #e9ecef;
  color: #333;
}

.nav-btn.active {
  background: #1976d2;
  color: #fff;
  font-weight: 500;
}

.employees-section h2 {
  color: #1976d2;
  margin-bottom: 1.5rem;
  text-align: center;
}

.employees-list {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  margin-bottom: 2rem;
}

.employee-card {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 12px;
  padding: 1.2rem;
  transition: all 0.2s;
}

.employee-card h3 {
  color: #333;
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.employee-form {
  display: flex;
  flex-direction: column;
  gap: 0.8rem;
}

.employee-summary {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #e0e0e0;
}

.payment-btn {
  background: #4caf50;
  color: #fff;
  border: none;
  border-radius: 6px;
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  cursor: pointer;
  transition: background 0.2s;
}

.payment-btn.paid {
  background: #666;
}

.payment-btn.unpaid {
  background: #ff9800;
}

.payment-btn:hover {
  opacity: 0.9;
}

.employees-summary {
  background: #e3f2fd;
  border-radius: 8px;
  padding: 1.2rem;
  margin-bottom: 1.5rem;
  border: 1px solid #bbdefb;
}

.employees-summary h3 {
  color: #1976d2;
  margin-bottom: 1rem;
  text-align: center;
}

.summary-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
  border-bottom: 1px solid #bbdefb;
  font-size: 1rem;
}

.feedback-section h2 {
  color: #1976d2;
  margin-bottom: 1.5rem;
  text-align: center;
}

.feedback-form {
  margin-bottom: 2rem;
}

.feedback-form textarea {
  width: 100%;
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  resize: vertical;
  min-height: 80px;
  font-family: inherit;
  text-align: right;
}

.feedback-form select {
  padding: 0.8rem;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  background: #fff;
  text-align: right;
}

.feedback-list h3 {
  color: #333;
  margin-bottom: 1rem;
  text-align: center;
}

.feedback-entry {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.feedback-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.satisfaction {
  padding: 0.3rem 0.6rem;
  border-radius: 12px;
  font-size: 0.85rem;
  font-weight: 500;
}

.satisfaction.satisfied {
  background: #e8f5e8;
  color: #2e7d32;
}

.satisfaction.unsatisfied {
  background: #ffebee;
  color: #c62828;
}

.feedback-entry p {
  margin: 0.5rem 0;
  color: #666;
  line-height: 1.5;
}

.feedback-entry small {
  color: #999;
  font-size: 0.8rem;
}

.reports-section h2 {
  color: #1976d2;
  margin-bottom: 1.5rem;
  text-align: center;
}

.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

.dashboard-grid .dashboard-item {
  background: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 1.2rem;
  text-align: center;
}

@media (max-width: 768px) {
  .app-nav {
    flex-wrap: wrap;
  }
  
  .nav-btn {
    flex: 1 1 calc(50% - 0.3rem);
    min-width: 120px;
  }
  
  .employee-summary {
    flex-direction: column;
    gap: 0.8rem;
    align-items: stretch;
  }
  
  .feedback-header {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }
  
  .dashboard-grid {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 480px) {
  .app-nav {
    flex-direction: column;
  }
  
  .nav-btn {
    flex: none;
    width: 100%;
  }
  
  .employee-card {
    padding: 1rem;
  }
  
  .feedback-entry {
    padding: 0.8rem;
  }
}

.employee-card,
.employee-card:hover,
.nav-btn,
.nav-btn:hover,
.payment-btn,
.payment-btn:hover,
.card,
.card:hover,
.form-row input:focus,
.form-row input:focus + .suggestions-dropdown,
.nav-btn:disabled,
.nav-btn:hover:disabled {
  transition: all 0.2s ease-in-out;
}

.form-error {
  animation: shake 0.5s ease-in-out;
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  75% { transform: translateX(5px); }
}

@media print {
  .app-nav,
  .delete-btn,
  .main-btn,
  .payment-btn {
    display: none !important;
  }
  
  .card {
    box-shadow: none;
    border: 1px solid #ccc;
  }
  
  .paid-by-issam {
    background: #e8f5e8 !important;
  }
}

